import  { mapActions } from 'vuex';
export default {

  // name: 'ComponentName',
  data () {
    return {
      isPwd: true,
      loading: false,
      tab: 'login',
      formDataLogin :
        {
          email : 'harry@email.com',
          password : '123456',

        },
      formDataRegister :
        {
          name : '',
          email : '',
          mobile : '',
        },
    }
  },
  methods:{
    ...mapActions('LedgerStore',['registerUser','loginUser']),
    closeModal(){
      this.$emit('updated',true)
    },
    onSubmitLogin(){

      this.loading = true;

      if(!this.$refs.password.hasError && !this.$refs.email.hasError){
        console.log('no error')
        this.$emit('updated',true)
        this.loginUser(this.formDataLogin);
        // this.loading = false;
      }

      // this.loading = false;
    },

    onSubmitReg(){

      if(!this.$refs.password.hasError && !this.$refs.email.hasError && !this.$refs.name.hasError){
        this.$emit('updated',true)
        this.loading = true;

        this.registerUser(this.formDataRegister);

        setTimeout(() => {
          // we're done, we reset loading state
          this.loading = false
        }, 800)

      }


    }


  }
}
